<template>
<div style="height:100%;">
<div class="main-container">
    <div class="main-div">
        <div class="main-content-c">
            <div style="margin-left:15%;width:60%;">
                <el-steps :active="1" finish-status="success" align-center>
                <el-step title="1. 产品排重检查已完成"></el-step>
                <el-step title="2. 产品信息录入进行中"></el-step>
                <el-step title="3. 发布产品未开始"></el-step>
                </el-steps>
            </div>
            <el-form :model="data" :rules="rules" ref="data">
            <p class="p-title">基础信息</p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">产品名称</el-col>
                <el-col :span="9">
                    <el-input v-model="data.name" placeholder="请输入内容" size="medium" disabled></el-input>
                </el-col>
                <el-col :span="2" class="name-right">产品分类</el-col>
                <el-col :span="9">
                    <el-cascader
                        size="medium"
                        style="width: 100%;"
                        :options="categoryList"
                        @change="handleChange"
                        ></el-cascader>
                </el-col>
                <i class="el-icon-plus" size="medium" style="font-size:14px;line-height:40px;" v-on:click="dialogFormVisible = true">新增分类</i>
                <!-- <el-button icon="el-icon-plus">新增分类</el-button> -->
                <el-dialog title="新增分类" :visible.sync="dialogFormVisible" width="30%">
                <el-form :model="form">
                    <el-form-item label="上级分类" :label-width="formLabelWidth">
                    <el-cascader
                        size="medium"
                        style="width: 100%;"
                        :options="form.cateList"
                        @change="handleChange"
                        ></el-cascader>
                        <!-- <el-select v-model="form.region" placeholder="请选择活动区域" style="width:100%;"> -->
                        <!-- <el-option label="区域一" value="shanghai"></el-option> -->
                        <!-- <el-option label="区域二" value="beijing"></el-option> -->
                        <!-- </el-select> -->
                    </el-form-item>
                    <el-form-item label="分类名称" :label-width="formLabelWidth">
                        <el-input v-model="form.name" auto-complete="off"></el-input>
                    </el-form-item>
                </el-form>
                <div slot="footer" class="dialog-footer">
                    <el-button @click="dialogFormVisible = false">取 消</el-button>
                    <el-button type="primary" @click="dialogFormVisible = false">确 定</el-button>
                </div>
                </el-dialog>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">搜索关键字</el-col>
                <el-col :span="9">
                    <el-input v-model="data.keyword" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                <el-col :span="2" class="name-right">品牌</el-col>
                <el-col :span="9">
                <el-select 
                    v-model="data.bid" 
                    placeholder="请选择" 
                    style="width:100%;" 
                    size="medium"
                    >
                <el-option
                   style="width: 100%;"
                   v-for="item in brandList"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
                </el-option>
                </el-select>
                </el-col>
                <i class="el-icon-plus" size="medium" style="font-size:14px;line-height:40px;">新增品牌</i>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">成本价</el-col>
                <el-col :span="9">
                    <el-input v-model="data.cost" placeholder="请输入价格" size="medium"></el-input>
                </el-col>
                
                
                <el-col :span="2" class="name-right">供应商</el-col>
                <el-col :span="9">
                <el-select size="medium" 
                v-model="data.sid_list" 
                multiple 
                placeholder="请选择" 
                style="width:100%;">
                <el-option
                     v-for="item in supplierList"
                     :key="item.value"
                     :label="item.label"
                     :value="item.value">
                </el-option>
                </el-select>
                </el-col>
                <i class="el-icon-plus" size="medium" style="font-size:14px;line-height:40px;">新增供应商</i>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">备注</el-col>
                <el-col :span="9">
                    <el-input v-model="data.remark" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                
                <el-col :span="2" class="name-right">公开程度</el-col>
                <el-col :span="9">
                <el-select v-model="data.open_level" placeholder="请选择" style="width:100%;" size="medium">
                <el-option
                   style="width: 100%;"
                   v-for="item in openList"
                   :key="item.value"
                   :label="item.label"
                   :value="item.value">
                </el-option>
                </el-select>               
                </el-col>
                
                </el-row>
            </p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">适用性别</el-col>
                <el-col :span="9" class="m_t_8px">
                    <el-radio v-model="data.sex" label="0">通用</el-radio>
                    <el-radio v-model="data.sex" label="1">男</el-radio>
                    <el-radio v-model="data.sex" label="2">女</el-radio>
                </el-col>
                <el-col :span="2" class="name-right">是否敏感</el-col>
                <el-col :span="9" class="m_t_8px">
                    <el-switch
                        v-model="data.is_sensitive"
                        active-text="是"
                        inactive-text="否">
                    </el-switch>
                </el-col>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            
            <p class="p-title">产品规格</p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">规格名称</el-col>
                <el-col :span="18">
                <el-checkbox-group  class="m_t_8px" v-model="checkList" :max=3>
                <el-checkbox v-for="(item, index) in attrList" :label="item.label" :value="item.value" v-on:click.native="chooseAttr(item.label, item.value, index)" ref="ddd"></el-checkbox>
                </el-checkbox-group>
                </el-col>

                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            <p class="p-content-main">
            <checkdiv v-for="item in divlist" :name="item.name" @check_value="get_value"></checkdiv>
            </p>
            <p class="p-title">申报信息</p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">中文申报名</el-col>
                <el-col :span="9">
                    <el-input v-model="data.declare_cname" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                <el-col :span="2" class="name-right">英文申报名</el-col>
                <el-col :span="9">
                    <el-input v-model="data.declare_ename" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="2" class="name-right">申报价格</el-col>
                <el-col :span="9">
                    <el-input v-model="data.declare_price" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                <el-col :span="2" class="name-right">申报编码</el-col>
                <el-col :span="9">
                    <el-input v-model="data.declare_code" placeholder="请输入内容" size="medium"></el-input>
                </el-col>
                <el-col :span="4">
                </el-col>
                </el-row>
            </p>

            <p class="p-title">产品图片</p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="24">
                <el-upload
                action="http://img.orkotech.com/fdfs_upload.php"
                :limit=5
                :data="uploadParam"
                :before-upload="beforeUpload"
                :on-success="uploadSuccess"
                :on-error="uploadFail"
                list-type="picture-card"
                :on-exceed="overLimit"
                :on-preview="handlePictureCardPreview"
                :on-remove="handleRemove">
                <i class="el-icon-plus"></i>
            </el-upload>
            <el-dialog :visible.sync="dialogVisible">
              <img width="100%" :src="dialogImageUrl" alt="">
            </el-dialog>
                </el-col>
                </el-row>
            </p>

            <p class="p-title">产品描述</p>
            <p class="p-content-main">
                <el-row :gutter="20">
                <el-col :span="22">
                <el-tabs type="border-card">
                    <el-tab-pane label="产品详情">
                    <UE id="editor" ref="ue1"></UE>
                    </el-tab-pane>
                    <el-tab-pane label="产品参数">
                    <UE id="editor2" ref="ue2"></UE>
                    </el-tab-pane>
                    <el-tab-pane label="选品思路">
                    <UE id="editor3" ref="ue3"></UE>
                    </el-tab-pane>
                </el-tabs>
                </el-col>
                </el-row>
            </p>
            <br>
            </el-form>
        </div>
        <div style="border-top:1px solid #ccc;">
            <el-button class="btn-save" @click="save('data')" plain>&nbsp;&nbsp;保存&nbsp;&nbsp;</el-button>
            <el-button class="btn-cancel" plain>&nbsp;&nbsp;取消&nbsp;&nbsp;</el-button>
        </div>
    </div>
    <!-- <button v-on:click="test()">Test</button> -->
</div>
</div>
</template>

<script>
import axios from 'axios'
import UE from 'util/UE.vue';
import checkdiv from 'page/oms_product/tools/checkdiv.vue';
import Loading from 'element-ui';

export default {
    components: {UE, checkdiv},
    data() {
        return {
            data:{
                name: '',
                cid: '',
                keyword: '',
                bid: '',
                open_level: 0,
                sid_list: [],
                sex: 0,
                is_sensitive: 0,
                attr_list: [],
                declare_cname: '',
                declare_ename: '',
                declare_code: '',
                declare_price: '',
                img_list: [],
                parameter: '1',
                description: '1',
                think: '1',
                cost: '',
                remark: '',
            },
            dialogImageUrl: '',
            dialogVisible: false,
            rules:{},
            checkAll: false,
            checkList: [],
            attrList: [],
            isIndeterminate: true,
            options:[],
            categoryList: [
            {
                value:'1',
                label:'111',
                children:[
                {
                    value:'2',
                    label:'222',
                    children:[
                    {
                        value:'3',
                        label:'333',
                        children:[
                        {
                            value:'4',
                            label:'444',
                        }
                        ]
                    }
                    ]
                },
                ]
            }
            ],
            brandList: [],
            supplierList: [],
            openList: [
            {
                value:'0',
                label:'所有',
            },
            {
                value:'1',
                label:'组内',
            },
            {
                value:'2',
                label:'私有',
            },
            ],
            options5: [{
                value: 'L',
                label: 'L'
            }, {
                value: 'S',
                label: 'S'
            }, {
                value: 'M',
                label: 'M'
            }],
            value10: [],
            value5: [],
            value: '',
            input: '',
            radio: '0',
            value3: false,
            dialogVisible: false,
            dialogTableVisible: false,
            dialogFormVisible: false,
            form: {
                name: '',
                cateList: '',
                delivery: false,
            },
            formLabelWidth: '120px',
            checkTime: 0,
            divlist:[
            ],
            ccc: false,
            uploadParam: {
                file_field: "file",//文件上传字段 将文件流保存到该值（eg:fdfs_file）
                token: "",//加密token md5( 加密salt值 + 13位时间戳 )
                time: +new Date(),//13位时间戳
                format: "",//上传的文件格式
                file: null//文件流
            },
        };
    },
    methods:{
        get_value(val) {
            let temp = {
                attr:val.name,
                value:val.list,
            };
            let list = this.data.attr_list;
            for (let item in list) {
                if (list[item].attr == val.name) {
                    this.data.attr_list.splice(item, 1);
                    this.data.attr_list.push(temp);
                }
            }
            console.log(this.data.attr_list);
        },
        save(formName) {
            let that = this;
            this.data.description = this.$refs.ue1.getUEContent();
            this.data.parameter = this.$refs.ue2.getUEContent();
            this.data.think = this.$refs.ue3.getUEContent();
            this.$refs[formName].validate((valid) => {
                if (valid) {
                    let url = '/v1/products';
                    this.$axios.post(
                        url,
                        that.data,
                    ).then((res) => {
                        if (res.status == 200) {
                            this.$router.push('/product/index');
                        } else {
                            this.$message.error(res.data.msg);
                        }
                    }).catch((err) => {
                        console.log(err);
                    });
                }
            });
        },
        chooseAttr(name, val, index) {
            let now = new Date();
            if ((now - this.checkTime) < 100) {
                return;
            }
            this.checkTime = now;
            let flag = this.$refs.ddd[index].isChecked;
            if (flag) {
                let tempList = this.divlist;
                let tempList2 = this.data.attr_list;
                tempList.forEach(function(row, k){
                    if (val == row.value) {
                        tempList.splice(k, 1);
                        tempList2.splice(k, 1);
                    }
                });
            } else {
                let length = this.divlist.length;
                if (length < 3) {//最多3个
                    this.divlist.push({'name':name, 'value': val});
                    this.data.attr_list.push({attr:name,value:[]});
                } else {
                    this.$message.error("最多选择3个规格");
                }
            }
        },
        handleCheckAllChange(val) {
            this.checkedCities = val ? cityOptions : [];
            this.isIndeterminate = false;
        },
        handleCheckedCitiesChange(value) {
            let checkedCount = value.length;
            this.checkAll = checkedCount === this.cities.length;
            this.isIndeterminate = checkedCount > 0 && checkedCount < this.cities.length;
        },
        open5() {
            this.$alert('<el-input v-model="input" placeholder="请输入内容" size="medium"></el-input>', '新增分类', {
                dangerouslyUseHTMLString: true
            });
        },
        open() {
            this.$alert('这是一段内容', '标题名称', {
                confirmButtonText: '确定',
                callback: action => {
                    this.$message({
                        type: 'info',
                        message: `action: ${ action }`
                    });
                }
            })
        },
        toggleSelection(rows) {
            if (rows) {
                rows.forEach(row => {
                    this.$refs.multipleTable.toggleRowSelection(row);
                });
            } else {
                this.$refs.multipleTable.clearSelection();
            }
        },
        handleSelectionChange(val) {
            this.multipleSelection = val;
        },
        beforeUpload(file) {
            this.uploadParam.token = this.$md5('salt001'+this.uploadParam.time.toString());
            let fileType = file.type;
            this.uploadParam.format = fileType.slice(fileType.indexOf('/')+1);
            this.uploadParam.file = file;
        },
        uploadSuccess(response, file, fileList) {
            if (response.status == 0) {
                this.data.img_list.push("http://img.orkotech.com/" + response.path);
            } else {
                this.$message.error(response.msg);
            }
        },
        uploadFail(err, file, fileList) {
            this.$message.error(err);
        },
        overLimit(file, fileList) {// 超出图片限制提示
            this.$message.error('最多上传 5 张图片');
        },
        handleRemove(file, fileList) {
            let temp = 'http://img.orkotech.com/' + file.response.path;
            for (let item in this.data.img_list) {
                if (this.data.img_list[item] == temp) {
                    this.data.img_list.splice(item, 1);
                }
            }
        },
        handlePictureCardPreview(file) {
            this.dialogImageUrl = file.url;
            this.dialogVisible = true;
        },
        initData() {
            let initList = [];
            let pid = this.$route.query.pid;
            let url = "/v1/product/initdatas?pid="+pid;
            let that = this;
            this.$axios.get(url).then((res) => {
                // ---------- START 分类选择数据 ----------
                let cate_list = res.data.data.cate_list;
                var list = [];
                cate_list.forEach(function(row, index) {
                    if (row.parent_id == 0) {
                        let temp = {
                            value:row.id,
                            label:row.name,
                            children:[]
                        };
                        cate_list.forEach(function(item, key){
                            if (item.parent_id == row.id) {
                                temp.children[key] = {};
                                temp.children[key].value = item.id;
                                temp.children[key].label = item.name;
                                cate_list.forEach(function(node, i){
                                    if(node.parent_id == item.id) {
                                        temp.children[key].children = [];
                                        temp.children[key].children[i] = {};
                                        temp.children[key].children[i].value = node.id;
                                        temp.children[key].children[i].label = node.name;
                                        cate_list.forEach(function(k, v){
                                            if (k.parent_id == node.id) {
                                            temp.children[key].children[i].children = [];
                                            temp.children[key].children[i].children[v] = {};
                                            temp.children[key].children[i].children[v].value = k.id;
                                            temp.children[key].children[i].children[v].label = k.name;
                                            }
                                        })
                                    }
                                });
                            }
                        });
                        list.push(temp);
                    }
                });
                that.categoryList = list;
                that.form.cateList = list;
                // ---------- END -----------  
                // ---------- START 品牌选择数据 ----------
                that.brandList = res.data.data.brand_list;
                // ---------- END ---------
                // ---------- START 品牌选择数据 ----------
                that.supplierList = res.data.data.supplier_list;
                // ---------- END ---------
                // ---------- START 规格选择数据 ----------
                that.attrList = res.data.data.attr_list;
                // ---------- END ---------
                that.data.name = res.data.data.check_data.name;
                that.data.img_list.push(res.data.data.check_data.img_list);
                // 待定
                //that.data.dialogImageUrl = res.data.data.check_data.img;
            }).catch((err) => {
                console.log(err);
            })
        },
        handleChange(value) {
            let length = value.length;
            this.data.cid = value[length-1];
        },
    },
    created() {
        this.initData();
    }
}
</script>

<style>
/* 设置table trhover 字体 */
/* 字体hover主色 13c19f */
/* hover背景色 eef1f9 */

.el-col {
    border-radius: 4px;
}
.grid-content {
    border-radius: 4px;
    min-height: 36px;
}
.row-bg {
    padding: 10px 0;
    background-color: #f9fafc;
}
.name-right{
    font-size:14px;
    line-height:34px;
    text-align:right;
}
.p-title{
    margin:0 auto;color:#03b8cc;
    margin-top:20px;
}
.p-content-main{
    margin-top:20px;
}
.m_t_8px{
    margin-top:8px;
}
.btn-save{
    float:right;margin:8px;background-color:#13c19f;color:white;border-color:#13c19f;
    margin-right:11%;
}
.btn-cancel{
    float:right;margin:8px;margin-right:20px;
}
.p-span{
    text-align:left;float:left;font-size:14px;line-height:20px;color:#96a6b6;
}
.p-span-active{
    color:black;
}
.el-icon-plus{
    cursor:pointer;
}
.avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
}
.avatar-uploader .el-upload:hover {
    border-color: #409EFF;
}
.avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 150px;
    height: 150px;
    line-height: 150px;
    text-align: center;
}
.avatar {
    width: 150px;
    height: 150px;
    display: block;
}
</style>



