<template>
<div>
    <el-row :gutter="20">
    <el-col :span="2" class="name-right" style="line-height:43px;">{{name}}</el-col>
        <el-col :span="12">
        <el-tag
              :key="tag"
              v-for="tag in dynamicTags"
              closable
              :disable-transitions="false"
              @close="handleClose(tag)">
        {{tag}}
        </el-tag>
        <el-input
              size="small"
              class="input-new-tag"
              v-if="inputVisible"
              v-model="inputValue"
              ref="saveTagInput"
              @keyup.enter.native="handleInputConfirm"
              @blur="handleInputConfirm"
              >
        </el-input>
        <el-button v-else class="button-new-tag" @click="showInput">+ New</el-button>
        </el-col>
    </el-row>
</div>
</template>
<script>

export default {
    name: 'checkdiv',
    data () {
        return {
            inputVisible: false,
            inputValue: '',
            dynamicTags: [],
        }
    },
    props: {
        name: {
            type: String,
            default: "颜色"
        }
    },
    mounted() {
    },
    destroyed() {
    },
    methods:{
        showInput() {
            this.inputVisible = true;
            this.$nextTick(_ => {
                this.$refs.saveTagInput.$refs.input.focus();
            });
        },
        handleClose(tag) {
            this.dynamicTags.splice(this.dynamicTags.indexOf(tag), 1);
        },
        handleInputConfirm() {
            let inputValue = this.inputValue;
            if (inputValue) {
                let flag = true;
                for(var item in this.dynamicTags) {
                    if (inputValue == this.dynamicTags[item]) {
                        flag = false;
                        this.$message.error("该值已存在，请勿重复");
                    } else {
                        flag = true;
                    }
                }
                if (flag) {
                    this.dynamicTags.push(inputValue);
                }
                if (this.dynamicTags == '') {
                    this.dynamicTags.push(inputValue);
                }
                this.$emit('check_value', {'list':this.dynamicTags, 'name':this.name});
            }
            this.inputVisible = false;
            this.inputValue = '';
        },
    }
}
</script>
<style>
.el-tag + .el-tag {
    margin-left: 10px;
    margin-top: 5px;
}
.button-new-tag{
  margin-left: 10px;
  height: 32px;
  line-height: 32px;
  padding-top: 0;
  padding-bottom: 0;
  margin-top: 5px;
}
.input-new-tag {
  width: 100px;
  margin-left: 10px;
  vertical-align: bottom;
  margin-top: 5px;
}
.el-tag--medium {
    height:32px;
    line-height: 34px;
    background-color:white;
}
</style>



